.widget {
  display: flex;
  justify-content: space-between;
  flex: 1;
  padding: 10px;
  -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
  box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
  border-radius: 10px;
  height: 100px;

  .left,
  .right {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;


    .title {
      align-items: center;
      margin-left: 40px;
      font-weight: bold;
      font-size: 14px;
      color: rgb(160, 160, 160);
    }

    .counter {
      font-size: 28px;
      font-weight: 700;
      margin-left: 50px;
    }

   

    .percentage {
      display: flex;
      align-items: center;
      font-size: 14px;

      &.positive {
        color: green;
      }

      &.negative {
        color: red;
      }
    }

    .icon {
      font-size: 40px;
      padding: 5px;
      margin-bottom: 15px;
      border-radius: 5px;
      align-self: flex-end;
    }
  }
  .link {
    width: max-content;
    font-size: 12px;
    border-bottom: 1px solid gray;
  }
}